import React from "react";

function About() {
  // For fade in and slide in animation

  const fader = document.querySelectorAll(".fade-in");

  const sliders = document.querySelectorAll(".slide-in");

  // observer for fade in animtion

  const appearOptions = {
    threshold: 0,
  };

  const appearOnScroll = new IntersectionObserver(function (
    entries,
    appearOnScroll
  ) {
    entries.forEach((entry) => {
      if (!entry.isIntersecting) {
        return;
      } else {
        entry.target.classList.add("appear");
        appearOnScroll.unobserve(entry.target);
      }
    });
  },
  appearOptions);

  fader.forEach((fader) => {
    appearOnScroll.observe(fader);
  });

  sliders.forEach((slider) => {
    appearOnScroll.observe(slider);
  });

  return (
    <div className="section" id="about">
      {/* <!-- ABOUT SECTION --> */}
      <h1 className="about-head fade-in">Who am I?</h1>
      <div className="my-self">
        <div className="my slide-in fade-in">
          <h1 className="name">Myself Moksh Singh Dangi</h1>
          <p>
            A 16 years-old boy with a dream of building best websites on the
            internet.
          </p>
        </div>
        <img
          src={require("../images/myself.png").default}
          alt=""
          className="about-img my-img slide-in fade-in"
        />
      </div>
      <div className="my-skill my-self ">
        <img
          className="about-img skills-img slide-in fade-in"
          src={require("../images/aboutSkill.png").default}
          alt=""
        />
        <p className="speciality slide-in fade-in">
          My speciality is front-end developement using React JS.
        </p>
      </div>
      {/* <!-- ABOUT SECTION ends --> */}
    </div>
  );
}

export default About;
