import React from "react";
import MenuIcon from "@material-ui/icons/Menu";
import HomeIcon from "@material-ui/icons/Home";
import VideocamIcon from "@material-ui/icons/Videocam";
import PersonIcon from "@material-ui/icons/Person";
import CallIcon from "@material-ui/icons/Call";
import Link from "@material-ui/icons/Link";

function Navbar() {
  function hideMenu() {
    let menuOpen = document.querySelector(".check").checked;

    if (menuOpen === true) {
      document.querySelector(".check").checked = false;
    }
  }
  window.addEventListener("scroll", hideMenu);

  return (
    <>
      {/* <!-- NAVIGATION BAR start here --> */}

      <div className="nav-bar">
        {/* <!-- logo --> */}
        <div className="logo">
          <a href="/my-web/#main-container">Moksh Singh Dangi</a>
        </div>
        {/* <!-- Nav-bar menu --> */}
        <nav className="navbar">
          <input type="checkbox" id="check" className="check" />
          <label for="check" className="checkbtn">
            <MenuIcon />
          </label>
          <ul className="menu-list">
            <li>
              <HomeIcon className="material-icons" />
              <a href="/my-web/#main-container">Home</a>
            </li>
            <li>
              <Link className="material-icons" />
              <a href="/my-web/#social">Social</a>
            </li>
            <li>
              <VideocamIcon className="material-icons" />
              <a
                href="https://www.youtube.com/channel/UCWS4SEXg_2DPRd4UiHFlCgQ"
                target="_blank"
                rel="noreferrer"
              >
                Youtube
              </a>
            </li>
            <li>
              <PersonIcon className="material-icons" />
              <a href="/my-web/#about">About</a>
            </li>
            <li>
              <CallIcon className="material-icons" />
              <a href="/my-web/#contact">Contact</a>
            </li>
          </ul>
        </nav>
      </div>
      {/* <!-- NAVIGATION BAR ends --> */}
    </>
  );
}

export default Navbar;
